.contacts {
  padding-bottom: 30px;

  min-height: 104px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  &--close {
    display: none;
  }
}

.contacts__link {
  color: #000000;
  margin-top: 40px;
  font-size: 20px;
  font-weight: normal;

  &--number {
    padding-bottom: 10px;
  }
}

.contacts__link:hover img,
.contacts__link:focus img {
  transform: scale(1.2);
}

@media (min-width: $desktop-width) {
  .contacts {
    margin-left: auto;
    padding: 0;
    padding-right: 40px;
    padding-left: 140px;
    min-width: 500px;
    min-height: 0;

    display: flex;
    flex-direction: row;
  }
}