.introduce {
  position: relative;
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: center;

  background-image: linear-gradient(rgba(00,00,00,.1) 0%, rgba(00,00,00,.6) 100%),url("../img/bg-header.jpg");
  background-position: center;
  background-size: cover;
}

.introduce__title {
  font-size: 40px;
  color: #ffffff;
  text-align: center;
}

.introduce__subtitle {
  margin-top: 40px;
  padding: 0 10px;
  font-size: 18px;
  color: #ffffff;
  text-align: center;
}

@media (min-width: $mobile-width) {
  .introduce__title {
    font-size: 50px;
  }

  .introduce__subtitle {
    font-size: 18px;
  }
}

@media (min-width: $tablet-width) {
  .introduce__title {
    font-size: 60px;
  }

  .introduce__subtitle {
    font-size: 20px;
  }
}

@media (min-width: $desktop-width) {
  .introduce__title {
    padding: 0 100px;
  }
}

@media (min-width: $desktop-width-L) {
  .introduce__subtitle {
    font-size: 24px;
  }
}
