.form__wrapper {
  padding: 40px 20px;
  background-color: rgb(247, 237, 230);
}

.form__title {
  margin-bottom: 40px;

  font-size: 30px;
  line-height: 36px;
  text-align: center;
}

.form__subtitle {
  font-size: 20px;
  text-align: center;
}

.form__fieldset {
  padding: 30px 20px 60px;

  height: 490px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.form__input {
  margin: 0;
  padding: 0;

  width: 100%;
  height: 60px;

  border: none;
  border-bottom: 1px solid rgb(140, 140, 140);

  font-family: inherit;
  font-size: 16px;

  outline: none;

  &--textarea {
    height: 100px;
    resize: none;
  }

  &:hover,
  &:focus {
    border-bottom: 2px solid rgb(0, 0, 0);
  }
}

.form__button {
  margin: 0 auto;
  width: 280px;
}

@media (min-width: $tablet-width) {
  .form__wrapper {
    padding: 60px 64px;
  }

  .form__title {
    font-size: 40px;
    line-height: 50px;
  }

  .form__fieldset {
    padding: 30px 64px 60px;
  }
}

@media (min-width: $desktop-width) {
  .form__title {
    padding: 0 180px;
  }

  .form__fieldset {
    padding: 30px 202px 60px;
  }
}

@media (min-width: $desktop-width-L) {
  .form__title {
    padding: 0 300px;
  }
}
