.reviews {
  position: relative;
  padding: 60px 20px;
}

.reviews__title {
  margin-bottom: 30px;
  font-size: 50px;
}

.reviews__container {
  min-width: 100%;
  overflow: hidden;
}

.reviews__track {
  display: flex;
  transition: .3s;
}

.reviews__item {
  padding: 20px;
  padding-right: 0;
  height: 280px;
}

.reviews__user-name {
  margin-bottom: 20px;
  display: inline-block;
  font-size: 20px;
}

.reviews__text {
  margin: 0;
  color: #333333;
  max-height: 190px;

  overflow: auto;

  & p {
    padding-right: 20px;
    margin: 0;
    font-size: 14px;
  }
}

.pre,
.next {
  padding: 0;
  position: absolute;
  top: 64%;
  width: 34px;
  height: 60px;

  transform: translateY(-50%);

  border: none;
  background-color: #2e466e;
  background-repeat: no-repeat;
  background-position: center;

  color: #ffffff;
  font-size: 0;

  z-index: 999;
  cursor: pointer;

  &:disabled {
    background-color: gray;
  }
}

.pre {
  left: 0;
  background-image: url("../img/icon-back.svg");
}

.next {
  right: 0;
  background-image: url("../img/icon-next.svg");
}

@media (min-width: $tablet-width) {
  .reviews {
    padding: 60px 64px;
  }

  .pre,
  .next {
    width: 50px;
  }

  .pre {
    left: 20px;
  }

  .next {
    right: 20px;
  }

  .reviews__text::-webkit-scrollbar {
    width: 4px;
  }

  .reviews__text::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  .reviews__text::-webkit-scrollbar-thumb {
    background: #888;
  }

  .reviews__text::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
}

@media (min-width: $desktop-width) {
  .reviews {
    padding: 60px 140px;
  }

  .pre {
    left: 60px;
  }

  .next {
    right: 60px;
  }
}

