.scroll {
  position: absolute;
  left: calc(50% - 13px);
  bottom: 30px;

  width: 30px;
  height: 20px;

  background-image: url("../img/icon-arrow.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  
  animation: scroll 1.6s infinite;
}

@keyframes scroll {
  0% {
    transform: translateY(-3px);
  }

  50% {
    transform: translateY(3px);
  }

  100% {
    transform: translateY(-3px);
  }
}

@media (min-width: $mobile-width) {
  .scroll {
    width: 50px;
    height: 30px;
  }
}
