.who {
  padding: 40px 20px;
}

.who__title {
  margin-bottom: 40px;

  font-size: 30px;
}

.who__item:last-child .who__text {
  margin-bottom: 0;
}

.who__subtitle {
  padding-left: 50px;

  background-image: url("../img/icon-check.svg");
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 30px 22px;
}

.who__text {
  margin-bottom: 40px;
  color: #444444;
}

@media (min-width: $mobile-width) {
  .who__title {
    font-size: 40px;
  }
}

@media (min-width: $tablet-width) {
  .who {
    padding: 60px 64px;
  }

  .who__title {
    margin-bottom: 60px;
    font-size: 50px;
  }
}

@media (min-width: $desktop-width) {
  .who {
    padding: 60px 64px 40px;
  }

  .who__title {
    text-align: center;
  }

  .who__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .who__item {
    max-width: 31%;
  }

  .who__text {
    margin-bottom: 60px;
  }
}