.footer {
  padding: 40px 20px;

  background-color: rgb(247, 237, 230);
}

.footer__contacts {
  position: relative;

  max-width: 280px;

  text-align: center;
  color: #000000;
  font-size: 30px;
  font-weight: bold;

  &:before {
    content: "";
    position: absolute;
    left: 0;
    bottom: -3px;

    width: 100%;
    height: 2px;
    background-color: #000000;

    transform: scaleX(0);
    transition: .2s;
    transform-origin: center;
  }

  &:hover,
  &:focus {
    color: blue;
  }

  &:hover:before,
  &:focus:before {
    transform: scaleX(1);
  }
}

.footer__address {
  margin: 45px 0;

  font-style: normal;
  font-size: 20px;
  line-height: 28px;
  text-align: center;
}

.footer__wrapper {
  text-align: center;

  &--contacts {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
}

@media (min-width: $desktop-width) {
  .footer {
    padding: 60px 64px;
    
    display: flex;
    justify-content: space-between;
  }

  .footer__wrapper {
    min-width: 280px;
    max-width: 280px;
  }

  .footer__address {
    margin: 0;
    max-width: 280px;

    border-left: 2px solid #333333;
    border-right: 2px solid #333333;
  }

  .footer__time {
    font-size: 20px;
  }
}
