.header {
  background-color: rgb(247, 237, 230);
}

.navigation {
  width: 100%;

  &--close {
    display: none;
  }
}

.navigation__wrapper {
  padding: 20px;
  background-color: #000000;

  display: flex;
  justify-content: space-between;
}

.navigation__logo {
  margin: 0;
  width: 100px;

  display: flex;
  align-items: center;

  color: #ffffff;
  font-weight: bold;
}

.navigation__button {
  padding: 0;
  border: none;
  font-size: 0;
  outline: none;

  width: 28px;
  height: 20px;

  background-color: transparent;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  background-position: center;

  cursor: pointer;

  &--menu {
    background-image: url("../img/icon-menu.svg");
  }
}

.navigation__list {
  padding: 40px 0;
  width: 100%;
}

.navigation__item {
  padding: 10px 0;

  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.navigation__link {
  text-decoration: none;
  color: #000000;
  font-weight: bold;

  transition: .2s;

  &:active {
    transform: scale(0.9);
    transition: .1s;
  }
}

.navigation__link--hover {
  position: relative;

  &:before {
    content: "";
    position: absolute;
    left: 0;
    bottom: -3px;

    width: 100%;
    height: 2px;
    background-color: #000000;

    transform: scaleX(0);
    transition: .2s;
    transform-origin: center;
  }

  &:hover:before,
  &:focus:before {
    transform: scaleX(1);
  }
}

@media (min-width: $mobile-width) {
  .navigation__link {
    font-size: 20px;
  }
}

@media (min-width: $desktop-width) {
  .header {
    display: flex;
    justify-content: space-between;
  }

  .navigation {
    display: flex;
  }

  .navigation__wrapper {
    padding-left: 20px;
    min-width: 160px;
    justify-content: flex-start;
  }

  .navigation__logo {
    min-width: 100%;
    font-size: 20px;
  }

  .navigation__button {
    display: none;
  }

  .navigation__list {
    padding: 0;
    display: flex;
  }

  .navigation__item {
    padding: 0;
    max-width: 130px;
  }
}
