.popup {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, .6);
  z-index: 999;

  display: none;
}

.popup__wrapper {
  position: relative;

  margin-left: 50%;
  transform: translate(-50%, 50%);

  padding: 20px;
  width: 90%;
  max-width: 500px;
  min-height: 300px;

  background-color: #fff;

  text-align: center;
}
 
.popup__title {
  font-size: 40px;
}

.popup__subtitle {
  margin-bottom: 108px;
  font-size: 20px;
}

.popup__button {
  min-width: 100%;
}

@media (min-width: $tablet-width) {
  .popup__wrapper {
    padding: 40px;
  }
}

@media (min-width: $desktop-width) {
  .popup__wrapper {
    max-width: 700px;
    padding: 40px 140px;
  }
}
