.help {
  background-color: rgb(247, 237, 230);
}

.help__wrapper {
  min-height: 440px;

  display: flex;
  flex-direction: column;
}

.help__title {
  padding: 60px 0;

  font-size: 30px;
  text-align: center;
}

.help__list {
  flex-grow: 1;

  display: flex;
  flex-direction: column;
  justify-content: space-around;

  text-align: center;
}

.help__item {
  padding: 20px;

  display: flex;
  align-items: center;
  justify-content: center;

  border-bottom: 1px solid #cdcdcd;

  line-height: 20px;

  &:first-child {
    border-top: 1px solid #cdcdcd;
  }
}

.help__strong {
  padding: 40px 20px;
}

.help__text {
  margin-bottom: 20px;

  display: inline-block;

  text-align: center;
  font-size: 18px;
  line-height: 26px;
}

@media (min-width: $mobile-width) {
  .help__title {
    font-size: 40px;
  }

  .help__text {
    font-size: 22px;
    line-height: 30px;
  }
}

@media (min-width: $tablet-width) {
  .help__wrapper {
    min-height: 256px;
    flex-direction: row;
    justify-content: center;

    & picture {
      max-width: 50%;
    }
  }

  .help__title {
    font-size: 50px;
  }

  .help__img {
    width: 100%;
    height: 100%;

    &--order {
      order: 1;
    }
  }

  .help__list {
    max-width: 50%;
    justify-content: space-between;

    &--order {
      order: -1;
    }
  }

  .help__item {
    padding: 10px;
    flex-grow: 1;
  }

  .help__strong {
    text-align: center;
  }
}

@media (min-width: $desktop-width) {
  .help__item {
    padding: 20px;
  }
} 

@media (min-width: $desktop-width-L) {
  .help__title {
    padding: 100px 0;
  }

  .help__item {
    font-size: 20px;
  }

  .help__strong {
    padding: 80px 0;
  }
}
