.button {
  margin: 0 auto;
  padding: 10px;

  min-width: 280px;
  max-width: 280px;
  height: 60px;

  display: flex;
  align-items: center;
  justify-content: center;

  border: none;
  background-color: #2e466e; 

  color: #ffffff;
  font-weight: bold;
  font-size: 16px;

  cursor: pointer;
  transition: .1s;

  &:hover {
    background-color: #259d;
  }
}

@media (min-width: $desktop-width-L) {
  .button {
    min-width: 300px;
    max-width: 300px;
  }
}