.about {
  padding: 60px 0 0;

  background-color: rgb(247, 237, 230);
}

.about__header {
  padding: 0 20px 60px;
}

.about__img {
  margin: 0 auto;
}

.about__title {
  position: relative;
  padding-bottom: 16px;

  margin: 30px;

  font-size: 30px;
  line-height: 36px;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: calc(50% - 25px);

    width: 50px;
    height: 3px;

    background-color: #2e466e;
  }
}

.about__description {
  margin-bottom: 30px;
  text-align: center;
  line-height: 23px;

  &--margin {
    margin-bottom: 0;
  }
}

.about__wrapper {
  padding: 60px 20px;

  background-image: url("../img/bg-page.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.about__content {
  padding: 24px 20px;
  background-color: #ffffff;
}

.about__text {
  line-height: 22px;
}

@media (min-width: $tablet-width) {
  .about__header {
    padding: 0 64px 60px;
  }

  .about__img {
    width: 100%;
  }

  .about__wrapper {
    padding: 24px;
  }

  .about__content {
    padding: 40px 60px;
  }

  .about__text {
    font-size: 18px;
    line-height: 28px;
  }
}

@media (min-width: $desktop-width) {
  .about__header {
    display: flex;
  }

  .about__img {
    min-width: 460px;
  }

  .about__me {
    min-width: 460px;
    margin-left: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .about__wrapper {
    padding: 40px 64px;
  }

  .about__button {
    margin: 0;
    margin-top: auto;
  }
}

@media (min-width: $desktop-width-L) {
  .about {
    padding-top: 90px;
  }

  .about__header {
    padding: 0 140px 90px;
  }

  .about__description {
    font-size: 20px;
  }

  .about__img {
    min-width: 560px;
  }

  .about__me {
    margin-left: 40px;
  }

  .about__wrapper {
    padding: 100px 140px;
  }
}
